<template>
  <div id="maintop">

    <b-input
      id="inline-form-input-name"
      class="mb-2 mr-sm-2 mb-sm-0 title"
      placeholder="请输入标题" v-model="title"
    ></b-input>

    <div id="main">
          <mavon-editor v-model="body" ref="md" @imgAdd="imgAdd" style="height: 100%"/>
    </div>

    <br>
    <b-input
      id="inline-form-input-name"
      class="mb-2 mr-sm-2 mb-sm-0"
      placeholder="tags  (输入英文标签，以空格分隔)" v-model="tags"
    ></b-input>

    <div class="btn">
      <b-button variant="outline-primary" @click="post">提交</b-button>
    </div>

  </div>
</template>

<script>
    export default {
        name: "Post",
      data() {
        return {
          db:{},
          body: '',
          title: '',
          tags: '',
        }
      },

      methods: {
        post(){
            let wif = this.$store.state.password
            let parentAuthor = ''
            let parentPermlink = this.tags.trim().split(/\s+/)[0]
            let author = this.$store.state.username
            let permlink = this.getstr()
            let title = this.title
            let body = this.body
            let tags = this.tags.trim().split(/\s+/)
            let jsonMetadata = {"tags": tags, "app": "steemit/1.0", "format": "markdown"}

            //发布到steem上
            this.postToSteem(wif, parentAuthor, parentPermlink, author, permlink, title, body, jsonMetadata)

            //发布到本地数据库
            //把数据拼成一个JSON
            let post = {
              "title": title,
              "author": author,
              "permlink": permlink,
              "body": body,
              "created": new Date().toISOString().split('.')[0],
              "json_metadata": JSON.stringify(jsonMetadata)
            }


          let that = this
          let db = this.$store.state.db
          let tableName = this.$store.state.username

          function newpost(){
            return new Promise(resolve => {
              //得到操作 'testTable' 表的事务对象
              let transaction = db.transaction([tableName], 'readwrite') //可读可写事务
              transaction.oncomplete = function (event) {
                console.log("事务操作完成")
              }
              transaction.onerror = function (event) {
                console.log("事务操作出错")
                console.dir(event)
              }
              transaction.onsuccess = function (event) {
                console.log('事务操作成功')
              }

              //从事务对象里得到表里的objectStore对象
              let objectStore = transaction.objectStore(tableName)
              objectStore.add(post)
              resolve()
            })
          }

          async function main(){
            await newpost()
            let posts = await that.getAllPosts(db)
            that.$store.commit('savePosts', posts.reverse())
            //发布完后跳转
            that.$router.push({path: '/article/'+permlink})

          }
          main()

          },

        imgAdd(pos, $file){
          // 第一步.将图片上传到服务器.
          var formdata = new FormData();
          formdata.append('smfile', $file);
          this.axios({
            url: 'https://sm.ms/api/v2/upload',
            method: 'post',
            data: formdata,
            headers: { 'Content-Type': 'multipart/form-data' },
          }).then((url) => {
            // 第二步.将返回的url替换到文本原位置![...](0) -> ![...](url)
            // $vm.$img2Url 详情见本页末尾
            // $vm.$img2Url(pos, url);
            this.$refs.md.$img2Url(pos, url.data.data.url)
          })
        },

        getStr(){
          function randomString(length, chars) {
            let result = ''
            for (var i = length; i > 0; --i) result += chars[Math.floor(Math.random() * chars.length)]
            return result;
          }
          this.permlink = randomString(5, '0123456789abcdefghijklmnopqrstuvwxyz')
        }

      },

      mounted() {
        let _this = this
        async function main(){
          if(_this.$store.state.username){
            //如果已登录
            if(_this.$store.state.posts == false){
              //第一次打开页面或是刷新
              let db = await _this.initDatabase()
              _this.$store.commit('saveDB',db)
              let posts = await _this.getAllPosts(db)
              _this.$store.commit('savePosts', posts.reverse())
            }
          }

        }
        main()
      }
    }
</script>

<style scoped>
  #maintop{
    width: 90%;
    height: 100%;
    margin: 0 auto;
  }

  #main{
    height: 40rem;
  }
  .title{
    margin-top: 1.5rem;
    margin-bottom: 1.5rem !important;
    width: 60%;
  }
  .btn{
    margin-top: 0.3rem;
  }
</style>



