import Vue from 'vue'
import Router from 'vue-router'
import Login from '../components/Login'
import Index from '../components/Index'
import Article from '../components/Article'
import Post from '../components/Post'
import Category from '../components/Category'
import Edit from '../components/Edit'
import Feed from '../components/Feed'
import OtherArticle from "../components/OtherArticle"
import OtherArticleHot from "../components/OtherArticleHot"
import Hot from "../components/Hot"
import Search from "../components/Search"
import Collection from "../components/Collection"
import CollectionArticle from "../components/CollectionArticle"
import SearchArticle from "../components/SearchArticle"
import AutoVote from "../components/AutoVote"
import ReplyToAuthor from "../components/ReplyToAuthor"
import ReplyDetail from "../components/ReplyDetail"
import Comments from "../components/Comments"
import CommentDetail from "../components/CommentDetail"
import About from "../components/footers/About"
import Newbie from "../components/footers/Newbie"



Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
      meta:{
        requireAuth:true
      }
    },
    {
      path: '/feed',
      name: 'feed',
      component: Feed,
      meta:{
        requireAuth:true
      }
    },
    {
      path: '/search',
      name: 'search',
      component: Search
    },
    {
      path: '/searcharticle/@:author/:permlink',
      name: 'searcharticle',
      component: SearchArticle
    },
    {
      path: '/collection',
      name: 'collection',
      component: Collection,
      meta:{
        requireAuth:true
      }
    },
    {
      path: '/autovote',
      name: 'autovote',
      component: AutoVote
    },
    {
      path: '/collectionarticle/@:author/:permlink',
      name: 'collectionarticle',
      component: CollectionArticle
    },
    {
      path: '/otherarticle/@:author/:permlink',
      name: 'otherarticle',
      component: OtherArticle
    },
    {
      path: '/otherarticlehot/@:author/:permlink',
      name: 'otherarticlehot',
      component: OtherArticleHot
    },
    {
      path: '/hot',
      name: 'hot',
      component: Hot
    },
    {
      path: '/category',
      name: 'category',
      component: Category,
      meta:{
        requireAuth:true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/article/:permlink',
      name: 'article',
      component: Article
    },
    {
      path: '/reply',
      name: 'replytoauthor',
      component: ReplyToAuthor,
      meta:{
        requireAuth:true
      }
    },
    {
      path: '/comments',
      name: 'comments',
      component: Comments,
      meta:{
        requireAuth:true
      }
    },
    {
      path: '/about',
      name: 'about',
      component: About
    },
    {
      path: '/newbie',
      name: 'newbie',
      component: Newbie
    },
    {
      path: '/replydetail/@:parent_author/:parent_permlink/@:author/:permlink',
      name: 'replydetail',
      component: ReplyDetail
    },
    {
      path: '/commentdetail/@:parent_author/:parent_permlink/@:author/:permlink',
      name: 'commentdetail',
      component: CommentDetail
    },
    {
      path: '/post',
      name: 'post',
      component: Post,
      meta:{
        requireAuth:true
      }
    },
    {
      path: '/edit/:permlink',
      name: 'edit',
      component: Edit,
      meta:{
        requireAuth:true
      }
    },
  ]
})



